@Component({
  selector: 'app-all-students',
  templateUrl: './all-students.component.html',
  styleUrls: ['./all-students.component.css']
})
export class AllStudentsComponent implements OnInit {
students = [];
delete= false;
  constructor( private studentService: StudentService, private http: HttpClient, private router: Router) { }

  ngOnInit(){
  this.getAllStudents();
  
 }
 getAllStudents(){ this.studentService.getStudents().subscribe((data: any[])=>{ 
  this.students= data});
  }

  
 deleteStud(student){ this.studentService.deleteStudent(student).subscribe((result)=>{
this.delete= true;
this.getAllStudents();
 });

 }

}