import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from "@angular/forms";
import { StudentService } from "../student.service";
import { ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-update-student',
  templateUrl: './update-student.component.html',
  styleUrls: ['./update-student.component.css']
})
export class UpdateStudentComponent implements OnInit {
datasaved= false;
studentForm= new FormGroup({
first_name: new FormControl(""),
middle_name: new FormControl(""),
last_name: new FormControl(""),
address_line1: new FormControl(""),
address_line2: new FormControl( ""),
address_line3: new FormControl(""),
city: new FormControl( ""),
district: new FormControl( ""),
state: new FormControl( "" ),
country: new FormControl( ""),
pincode: new FormControl( ""),
gender: new FormControl(""),
dob: new FormControl(""),
mob: new FormControl("")
});
  
constructor( private studentService: StudentService, private router: ActivatedRoute) { }
  ngOnInit(): void { this.studentService.getStudent(this.router.snapshot.params.id).subscribe((result)=>{
 this.studentForm= new FormGroup({
first_name: new FormControl( result["first_name"]),
middle_name: new FormControl( result["middle_name"]),
last_name: new FormControl( result["last_name"]),
address_line1: new FormControl( result["address_line1"]),
address_line2: new FormControl( result["address_line2"]),
address_line3: new FormControl( result["address_line3"]),
city: new FormControl( result["city"]),
district: new FormControl( result["district"]),
state: new FormControl( result["state"]),
country: new FormControl( result["country"]),
pincode: new FormControl( result["pincode"]),
gender: new FormControl( result["gender"]),
dob: new FormControl( result["dob"]),
mob: new FormControl( result["mob"])
});
  });
  }
onFormSubmit(){
console.log(this.studentForm.value);
this.studentService.editStudent(this.router.snapshot.params.id, this.studentForm.value).subscribe((result)=>{console.warn(result);
this.datasaved= true;
});

}
}